import cluster, { Worker } from 'cluster';
import process from 'node:process';

export type processOnEvent = NodeJS.Signals | 'uncaughtException';

export interface NodeWrapperInterface {
  process: {
    pid: number;
    env: NodeJS.ProcessEnv;
    on: (event: processOnEvent, listener: (arg: unknown) => void) => void;
    exit: (code?: number) => void;
  };

  cluster: {
    isPrimary: boolean;
    workers: NodeJS.Dict<Worker> | undefined;
    fork: (env?: NodeJS.ProcessEnv) => Worker;
  };
}

export class NodeWrapper implements NodeWrapperInterface {
  readonly process = {
    pid: process.pid,
    env: process.env,
    on: process.on,
    exit: process.exit,
  };

  readonly cluster = {
    isPrimary: cluster.isPrimary,
    workers: cluster.workers,
    fork: cluster.fork,
  };
}
